<template>
  <AntSpace direction="vertical" style="width: 100%; padding: 0px 0px 20px 0px">
    <AntTypographyTitle>Logs</AntTypographyTitle>
    <AntForm v-if="showAllFilters" layout="vertical">
      <AntRow :gutter="10">
        <AntCol :span="12">
          <AntFormItem label="Run ID">
            <AntInput
              v-model:value="state.filters.search"
              placeholder="Search by Run ID"
              style="width: 100%"
            />
          </AntFormItem>
        </AntCol>
        <AntCol :span="12">
          <AntFormItem label="Stage">
            <AntSelect
              v-model:value="state.filters.stageId"
              :options="state.filterOptions.stages"
              placeholder="All"
              allow-clear
            />
          </AntFormItem>
        </AntCol>
      </AntRow>
      <AntRow :gutter="10">
        <AntCol :span="8">
          <AntFormItem label="Date">
            <AntRangePicker v-model:value="state.filters.dateRange" style="width: 100%" />
          </AntFormItem>
        </AntCol>
        <AntCol :span="10">
          <AntFormItem label="Version">
            <AntSelect
              v-model:value="state.filters.buildId"
              :options="state.filterOptions.builds"
              option-label="label"
              option-value="value"
              :filter="false"
              placeholder="All"
              allow-clear
            />
          </AntFormItem>
        </AntCol>
        <AntCol :span="6">
          <AntFormItem label="Status">
            <AntSelect
              v-model:value="state.filters.status"
              :options="state.filterOptions.statuses"
              option-label="label"
              option-value="value"
              :filter="false"
              placeholder="All"
              allow-clear
            />
          </AntFormItem>
        </AntCol>
      </AntRow>
    </AntForm>
    <AntForm v-else layout="vertical">
      <AntRow :gutter="10">
        <AntCol :span="8">
          <AntFormItem label="Date">
            <AntRangePicker v-model:value="state.filters.dateRange" style="width: 100%" />
          </AntFormItem>
        </AntCol>
        <AntCol :span="10">
          <AntFormItem label="Script">
            <AntSelect
              v-model:value="state.filters.stageId"
              :options="state.filterOptions.stages"
              placeholder="All"
              allow-clear
            />
          </AntFormItem>
        </AntCol>
        <AntCol :span="6">
          <AntFormItem label="Status">
            <AntSelect
              v-model:value="state.filters.status"
              :options="state.filterOptions.statuses"
              option-label="label"
              option-value="value"
              :filter="false"
              placeholder="All"
              allow-clear
            />
          </AntFormItem>
        </AntCol>
      </AntRow>
    </AntForm>
    <AntSpin
      v-if="state.currentPage.loadingExecutions || state.currentPage.waitingDebounce"
      style="width: 100%"
    />
    <AntSpace
      v-else-if="state.currentPage.executions && state.currentPage.executions.length > 0"
      direction="vertical"
      style="width: 100%"
    >
      <AntCollapse
        v-model:active-key="state.currentPage.openedExecutionIds"
        :bordered="false"
        style="background-color: transparent"
        @change="controller.fetchEmptyLogs"
      >
        <AntCollapsePanel
          v-for="execution in state.currentPage.executions"
          :key="execution.id"
          style="
            border-radius: 4px;
            margin-bottom: 10px;
            border: 0;
            overflow: hidden;
            background-color: #fff;
          "
        >
          <template #header>
            <AntTag color="default" style="margin-right: 10px" :bordered="false">
              {{ getLocalizedString(execution.createdAt) }}
            </AntTag>
            <AntTag color="default" style="margin-right: 10px" :bordered="false">
              Run ID: {{ execution.id.slice(0, 8) }}
            </AntTag>
            <AntTag color="default" style="margin-right: 10px" :bordered="false">
              Version: {{ execution.buildId.slice(0, 8) }}
            </AntTag>
            <AntTag color="default" style="margin-right: 10px" :bordered="false">
              Duration: {{ execution.duration_seconds }}
            </AntTag>
            <AntTag color="default" style="margin-right: 10px" :bordered="false">
              {{
                state.filterOptions.stages.find((option) => option.value === execution.stageId)
                  ?.label ?? execution.stageId.slice(0, 8)
              }}
            </AntTag>
          </template>
          <template #extra>
            <div
              style="
                width: 100px;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: right;
                gap: 10px;
              "
            >
              <AbstraButton
                type="text"
                size="small"
                tooltip="Copy URL"
                @click.stop="copyExecutionUrl(execution.id)"
              >
                <PhLink />
              </AbstraButton>
              {{ capitalizeFirstLetter(execution.status) }}
              <ExecutionStatusIcon :status="execution.status" />
            </div>
          </template>
          <div
            v-if="!state.currentPage.executionData[execution.id]"
            style="display: flex; width: 100%; justify-content: center"
          >
            <AntSpin />
          </div>
          <AntFlex v-else vertical gap="middle">
            <ThreadData
              :data="
                computeExecutionContext(
                  execution.context,
                  state.currentPage.executionData[execution.id].tasks,
                )
              "
              :failed="false"
            />
            <LogContainer :logs="state.currentPage.executionData[execution.id].logs" />
          </AntFlex>
        </AntCollapsePanel>
      </AntCollapse>
      <AntPagination
        v-model:current="state.pagination.currentIndex"
        v-model:page-size="state.pagination.pageSize"
        :total="state.pagination.totalCount"
        :show-total="(total) => `Found ${total} runs`"
        show-size-changer
      />
    </AntSpace>
    <AntEmpty v-else />
  </AntSpace>
</template>

<script lang="ts" setup>
import { BuildRepository } from '@/apps/console/apis/cloud';
import { ClientContext, ExecutionRepository, ExecutionStatusType } from '@/models/executions';
import {
  Col as AntCol,
  Collapse as AntCollapse,
  CollapsePanel as AntCollapsePanel,
  Empty as AntEmpty,
  Flex as AntFlex,
  Form as AntForm,
  FormItem as AntFormItem,
  Input as AntInput,
  Pagination as AntPagination,
  RangePicker as AntRangePicker,
  Row as AntRow,
  Select as AntSelect,
  Space as AntSpace,
  Spin as AntSpin,
  Tag as AntTag,
  TypographyTitle as AntTypographyTitle,
  message,
} from 'ant-design-vue';
import { useRoute, useRouter } from 'vue-router';
import { ContextTasks, FiltersState, LogsController } from './LogsController';

import ExecutionStatusIcon from '@/apps/console/components/buildInspector/ExecutionStatusIcon.vue';
import AbstraButton from '@/components/AbstraButton.vue';
import { getLocalizedString } from '@/utils/datetime';
import { capitalizeFirstLetter } from '@/utils/string';
import { PhLink } from '@phosphor-icons/vue';
import dayjs from 'dayjs';
import { onUnmounted } from 'vue';
import ThreadData from './ExecutionContext.vue';
import LogContainer from './LogContainer.vue';

const props = defineProps<{
  executionRepository: ExecutionRepository;
  buildRepository: BuildRepository | null;
  showAllFilters: boolean;
}>();

const route = useRoute();
const router = useRouter();
const projectId = route.params.projectId as string;
const executionId = route.query.executionId as string;

function getQueryFilters() {
  const { stageId, buildId, status, startDate, endDate, runId } = route.query;

  if (startDate && endDate) {
    return {
      stageId: stageId as string,
      buildId: buildId as string,
      status: status as ExecutionStatusType,
      dateRange: [dayjs(startDate as string), dayjs(endDate as string)] as [
        dayjs.Dayjs,
        dayjs.Dayjs,
      ],
    };
  }

  return {
    stageId: stageId as string,
    buildId: buildId as string,
    status: status as ExecutionStatusType,
    search: runId as string,
  };
}

function updateQueryFilters(filters: FiltersState) {
  const query: Record<string, string> = {};

  Object.entries(filters).forEach(([key, value]) => {
    if (typeof value === 'string' && key === 'search' && value) {
      query['runId'] = value;
    } else if (typeof value === 'string' && value) {
      query[key] = value;
    } else if (key === 'dateRange' && value && dayjs.isDayjs(value[0]) && dayjs.isDayjs(value[1])) {
      query['startDate'] = value[0].toISOString();
      query['endDate'] = value[1].toISOString();
    }
  });

  router.replace({ query });
}

function getQueryPagination() {
  const { page, pageSize } = route.query;

  return {
    page: page ? parseInt(page as string, 10) : 1,
    pageSize: pageSize ? parseInt(pageSize as string, 10) : 10,
  };
}

function copyExecutionUrl(executionId: string) {
  const runId = executionId.slice(0, 6);
  const url = `${window.location.origin}${window.location.pathname}?runId=${runId}`;
  navigator.clipboard.writeText(url);
  message.success({
    content: 'Copied to clipboard',
  });
}

function computeExecutionContext(executionContext: ClientContext, tasks: ContextTasks) {
  let context = { ...executionContext } as Record<string, unknown>;
  context['sentTasks'] = tasks.sentTasks;
  delete context['mockExecution'];
  if (tasks.triggerTask) {
    context['triggerTask'] = tasks.triggerTask;
    delete context['taskId'];
  }
  return context;
}

const controller = new LogsController(
  props.executionRepository,
  props.buildRepository,
  projectId,
  getQueryFilters(),
  getQueryPagination(),
  executionId,
  updateQueryFilters,
);

const { state } = controller;

controller.init();

onUnmounted(() => controller.teardown());
</script>
